    background: -webkit-linear-gradient(top, rgba(142, 142, 142, 1) 0%, rgba(37, 69, 88, 1) 67%, rgba(43, 43, 58, 1) 100%);
    background: -o-linear-gradient(top, rgba(142, 142, 142, 1) 0%, rgba(37, 69, 88, 1) 67%, rgba(43, 43, 58, 1) 100%);
    background: linear-gradient(180deg, rgba(142, 142, 142, 1) 0%, rgba(37, 69, 88, 1) 67%, rgba(43, 43, 58, 1) 100%);
    /* margin: -10px;
